.container {
  max-width: 400px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  margin-top: 0;
}

.search-container {
  margin-bottom: 20px;
  position: relative;
}

.breed-select {
  width: 200px;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.loader {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 0 auto;
  animation: loader-spin .8s linear infinite;
  display: block;
}

@keyframes loader-spin {
  to {
    transform: rotate(360deg);
  }
}

.cat-info {
  text-align: center;
  margin-top: 20px;
  display: none;
}

.cat-image {
  width: 200px;
  height: 200px;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  margin: 0 auto;
}

.cat-details {
  margin-top: 10px;
}

.cat-name {
  margin: 0;
}

.error-message {
  color: red;
  margin-top: 20px;
  font-weight: bold;
  display: none;
}

